<template>
  <div id="app">
    <Excel></Excel>
    <Panle></Panle>
  </div>
</template>

<script>
import Excel from './components/Excel'
import Panle from './components/Panle'
import { mapActions } from 'vuex'

export default {
  name: 'app',
  components: {
    Excel,
    Panle
  },
  methods: mapActions([
    'initDatas',
  ]),
  created: function() {
    this.initDatas();
  }
}
</script>

<style>
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



